import React from 'react'
import {
  CAvatar,
  CBadge,
  CDropdown,
  CDropdownDivider,
  CDropdownHeader,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
} from '@coreui/react'
import {
  cilBell,
  cilCreditCard,
  cilCommentSquare,
  cilEnvelopeOpen,
  cilFile,
  cilLockLocked,
  cilSettings,
  cilTask,
  cilUser,
} from '@coreui/icons'
import CIcon from '@coreui/icons-react'
import { BrowserRouter as Router,  Routes,useNavigate,  Route,  Link } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'
import { decrement, login } from '../../auth/reducers/index'


//import avatar8 from './../../tnssp/imgres/tn_logo.png'

const AppHeaderDropdown = () => {
  let navigate = useNavigate();
  const dispatch = useDispatch()
  const log_out=(e)=>{
    console.log('logged Out')
    dispatch(login({message:'Logged Out',userName:'',password:''}))
    navigate('/login',{})
  }
  return (
    <CDropdown variant="nav-item">
      <CDropdownToggle placement="bottom-end" className="py-0" caret={false}>
      
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownHeader className="bg-light fw-semibold py-2">Account</CDropdownHeader>
        
        
        <CDropdownItem href="#" onClick={(e)=>{ log_out(e)}}>
          <CIcon icon={cilLockLocked} className="me-2" />
        Log Out
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  )
}

export default AppHeaderDropdown
