import React from 'react'
import { NavLink } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  CContainer,
  CHeader,
  CHeaderBrand,
  CHeaderDivider,
  CHeaderNav,
  CHeaderToggler,
  CNavLink,
  CNavItem,
  CCollapse,
  CDropdown,
  CDropdownItem,
  CDropdownToggle,
  CDropdownMenu,
  CButton,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilBell, cilEnvelopeOpen, cilList, cilMenu } from '@coreui/icons'
import {
  BrowserRouter as Router,
  Routes,
  useNavigate,
  useLocation,
  Route,
  Link,
} from 'react-router-dom'
import { AppBreadcrumb } from './index'
import { AppHeaderDropdown } from './header/index'
import { logo } from 'src/assets/brand/logo'
import { decrement, increment, setSidebar } from '../auth/reducers/index'
import { useEffect, useState } from 'react'
const AppHeader = () => {
  const dispatch = useDispatch()
  const sidebarShow = useSelector((state) => state.sidebarShow)
  const data = useSelector((state) => state.twad)
  let navigate = useNavigate()
  function headerDetals() {
    console.log(data, 'dartagf')
  }

  useEffect(() => {
    headerDetals()
  }, [])
  function logoutClick() {
    navigate('/', { replace: true })
  }
  return (
    <CHeader position="sticky" className="mb-4 sidebar-color">
      <CContainer fluid>
        <CHeaderToggler
          className="ps-1"
          //  onClick={() => dispatch({ type: 'set', sidebarShow: !sidebarShow })}
          onClick={() => dispatch(setSidebar())}
        >
          <CIcon icon={cilMenu} size="lg" />
        </CHeaderToggler>
        <CHeaderBrand className="mx-auto d-md-none" to="/">
          <CIcon icon={logo} height={48} alt="Logo" />
        </CHeaderBrand>

        <CHeaderNav className="d-none d-md-flex me-auto">
          <CNavItem>
            <CNavLink to="/pension" component={NavLink}>
              <h3 className="text-white"> TWAD-PENSION/FAMILY PENSION</h3>
            </CNavLink>
          </CNavItem>
          {/* <CNavItem>
                    <CNavLink href="#"></CNavLink>
                  </CNavItem> */}
        </CHeaderNav>
        <div className="text-decoration-none">
          Name: {data.employeeName}
          <CNavLink href="#">Office Name: {data.office.officeName}</CNavLink>
        </div>
        <CButton className="text-white mx-2" onClick={logoutClick}>
          Logout
        </CButton>
      </CContainer>
    </CHeader>
  )
}

export default AppHeader
