import React, { useEffect, useState } from 'react';
import { useSelector, } from 'react-redux'
// import { AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/styles/ag-grid.css';
import 'ag-grid-community/styles/ag-theme-alpine.css';
// import axios from 'axios';
// import ReactApexChart from 'react-apexcharts';
// import Chart from 'react-apexcharts';
import ServiceURL from '../config/ServiceURL';
import CallApi from '../../src/util/APICall'
// import { useSelector, useDispatch } from 'react-redux';
import '../scss/report.css';
import {
    CTable,
    CTableDataCell,
    CTableRow,
    CTableHeaderCell,
    CTableBody,
    CSpinner,
    CFormSelect,
    CButton,
    CModalBody,
    CTableHead,
    CModal, CModalHeader
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import { cilCloudDownload } from '@coreui/icons';
// import { element } from 'prop-types';
// import { current } from '@reduxjs/toolkit';

const ReportSchemeWise = () => {
    const [loading, setLoading] = useState(false);
    const [tableRow, setTableRow] = useState([]);
    // const [selectedMonth, setSelectedMonth] = useState(0)
    // const [selectedYear, setSelectedYear] = useState(0)
    const [tableTwad, setTableTwad] = useState([])
    const [visible, setVisible] = useState(false)
    const [schemeName, setSchemeName] = useState('')
    const [dcbBreakup, setDcbBreakUp] = useState(false);
    const [sort, setSort] = useState(null)
    const [schOffice, setSchOffice] = useState(null)
    const [officeList, setOfficeList] = useState([])
    const [error, setError] = useState(false)
    const data = useSelector((state) => state.twad)
    const officeId = data.office.officeId || 5000;
    let officeLevelId = data.office.officeLevelId
    let currentDate = new Date();
    let currentMonth = currentDate.getMonth();
    let currentYear = currentDate.getFullYear();
    let serialNumberTwad = 1;
    // function handleChange(event) {
    //     setSelectedYear(event.target.value);
    //     }
    // function handleChange1(event) {
    //     setSelectedMonth(event.target.value);
    //  }
    // console.log(selectedYear)
    // console.log(selectedMonth)

    // const handleSubmit = () => {
    //     let url = ServiceURL.schemesWise;
    //     const userData = {
    //         demandYear: selectedYear,
    //         demandMonth: selectedMonth

    //     };
    //     let p = CallApi.callEndPoint(url, userData);

    //     p.then((response) => {
    //         setTableRow(response.response);
    //         console.log(tableRow)


    //     }).catch((errors) => {
    //         console.log(errors, "errors");
    //     });


    // }

    console.log(officeLevelId)

    // if(officeLevelId === 'DN'){
    //     setSchOffice(officeId)
    // }

  

    const twadDetails = (val1, val2, val3, val4, val5, val6) => {
        let url = ServiceURL.schemesWise;
        const userData = {
            demandYear: currentYear,
            demandMonth: currentMonth,
            schemeId: val1,
            flag: val2,
            typeOfConnection: val3,
            officeId :officeLevelId === 'DN' ? officeId : schOffice !== null ?  schOffice :null
        };
        if (val5 === 'dcb' && val6 !== null) {
            setDcbBreakUp(true);

        }
        else {
            setVisible(true)
            setDcbBreakUp(false);

        }
        console.log(dcbBreakup)
        setSchemeName(val4)
        let p = CallApi.callEndPoint(url, userData);

        p.then((response) => {

            setTableTwad(response.response);
            console.log(tableTwad)
        }

        ).catch((errors) => {
            console.log(errors, "errors");
        });


    }

    const exportSubTableData = () => {
        const rows = document.querySelectorAll('.table tbody tr');

        // Create a CSV formatted string
        let csvContent = "data:text/csv;charset=utf-8,";
        csvContent += "S.NO,Scheme Id,Scheme Type,Scheme Name,No.of.HT,Demand Amount (Rupees in Lakhs),No.Of LT,Demand Amount(Rupees in Lakhs),Demand Amount(Average per month cost),Total Connections,Total Amount(Rupees in Lakhs),DCB Demand Raised(Rupees in Lakhs),Percentage of EB to DCB \r\n";
        rows.forEach((row, index) => {
            const serialNumber = index + 1;
            alert(row.querySelector('td:nth-child(2)'))
            const schemeIdCell = row.querySelector('td:nth-child(2)');
            const schemeTypeCell = row.querySelector('td:nth-child(3)');
            const schemeNameCell = row.querySelector('td:nth-child(4)');
            const noOfHTCell = row.querySelector('td:nth-child(5)');
            const demandAmountCell1 = row.querySelector('td:nth-child(6)');
            const noOfLTCell = row.querySelector('td:nth-child(7)');
            const demandAmountCell2 = row.querySelector('td:nth-child(8)');
            const averageDemandAmountCell = row.querySelector('td:nth-child(9)');
            const totalConnectionsCell = row.querySelector('td:nth-child(10)');
            const totalAmountCell = row.querySelector('td:nth-child(11)');
            const dcbDemandCell = row.querySelector('td:nth-child(12)');
            const percentCell = row.querySelector('td:nth-child(13)');

            // Check if any cell is null
            if (
                !schemeIdCell || !schemeTypeCell || !schemeNameCell ||
                !noOfHTCell ||
                !demandAmountCell1 ||
                !noOfLTCell ||
                !demandAmountCell2 ||
                !totalConnectionsCell || !averageDemandAmountCell ||
                !totalAmountCell || !dcbDemandCell || !percentCell

            ) {
                console.error(`One or more cells not found in row ${index + 1}`);
                return; // Move to the next row
            }
            const schemeId = schemeIdCell.textContent.trim().replace(/,/g, '');
            const schemeType = schemeTypeCell.textContent.trim().replace(/,/g, '');
            const schemeName = schemeNameCell.textContent.trim().replace(/,/g, '');
            const noOfHT = noOfHTCell.textContent.trim().replace(/,/g, ''); // Remove commas
            const demandAmount1 = demandAmountCell1.textContent.trim().replace(/,/g, ''); // Remove commas
            const noOfLT = noOfLTCell.textContent.trim().replace(/,/g, ''); // Remove commas
            const demandAmount2 = demandAmountCell2.textContent.trim().replace(/,/g, ''); // Remove commas
            const averageAmount = averageDemandAmountCell.textContent.trim().replace(/,/g, '');
            const totalConnections = totalConnectionsCell.textContent.trim().replace(/,/g, ''); // Remove commas
            const totalAmount = totalAmountCell.textContent.trim().replace(/,/g, ''); // Remove commas
            const dcbDemand = dcbDemandCell.textContent.trim().replace(/,/g, '');
            const percent = percentCell.textContent.trim().replace(/,/g, '');

            const rowString = `${serialNumber},"${schemeId}","${schemeType}","${schemeName}",${noOfHT},${demandAmount1},${noOfLT},${demandAmount2},${averageAmount},${totalConnections},${totalAmount},${dcbDemand},${percent}\r\n`;
            csvContent += rowString;
        });

        // Create a link element and trigger a download
        const encodedUri = encodeURI(csvContent);
        const link = document.createElement("a");
        link.setAttribute("href", encodedUri);
        link.setAttribute("download", "ReportScheme.csv");
        document.body.appendChild(link); // Required for Firefox
        link.click();
    };
    function getOffice() {


        let url = ServiceURL.officeList;
        const userData1 = {
            officeList: "getOffice"
        }
        let p = CallApi.callEndPoint(url, userData1);
        p.then((response) => {
            console.log(response)
            setOfficeList(response.response)
        })

        console.log(officeList)
    }




    useEffect(() => {
        const timeout = setTimeout(() => {
            setLoading(false);
        }, 5000);

        let url = ServiceURL.schemesWise;
        const userData = {
            demandYear: currentYear,
            demandMonth: currentMonth,
            sortColumn: sort,
            officeId: officeLevelId === 'DN' ? officeId : schOffice

        };
        let p = CallApi.callEndPoint(url, userData);

        p.then((response) => {
            setTableRow(response.response);
            setError(false)
            //console.log(tableRow)
        }).catch((errors) => {
            setError(true)
            console.log(errors, "errors");
        });


        getOffice();
        return () => clearTimeout(timeout);

    }, [sort, schOffice]);






    return (
        <div >
            {loading ? (
                <div className="d-flex justify-content-center align-items-center" style={{ height: '100vh' }}>
                    <CSpinner color="primary" variant="grow" />
                </div>
            ) : (
                <div className='body-container' >
                    <div className='row justify-content-end mt-2 '>

                        <div className='justify-content-end d-flex mt-2'>


                            {officeLevelId === 'DN' ? '' :
                                <>

                                    <label className='lbl'>OfficeWise</label>

                                    <CFormSelect aria-label="Default select example" style={{ width: '200px', display: 'inline-block', marginRight: '1.5em' }} onChange={(e) => { setSchOffice(e.target.value) }}>

                                        <option>Select Office </option>
                                        {officeList.map((element) => (
                                            <option value={element.office_id}>{element.office_name}</option>))}

                                    </CFormSelect>

                                </>}
                            <label className='lbl'>Order By</label>
                            <CFormSelect aria-label="Default select example" style={{ width: '200px', display: 'inline-block', marginRight: '1.5em' }} onChange={(e) => { setSort(e.target.value) }}>

                                <option>Select Order </option>
                                <option value="total_amount">EB</option>
                                <option value="percentage">EB/DCB</option>
                            </CFormSelect>

                            <CButton onClick={exportSubTableData} color='warning' className='rounded'>
                                <CIcon icon={cilCloudDownload} className='text-white' />
                                <span className='text-white mx-2'>Export Data</span>
                            </CButton>
                        </div>
                        {/* <div className='sort-class'>
                            
                            <CFormSelect aria-label="Default select example" style={{ width: '200px', display:'inline-block' }} onChange={(e)=>{setSort(e.target.value)}}>
                               
                                <option >Select Order </option>
                                <option value="total_amount">EB</option>
                                <option value="percentage">EB/DCB</option>                          
                             </CFormSelect>
                            </div> */}

                        {/* <div >
                        <CFormSelect aria-label="Default select example" style={{ width: '200px', display:'inline-block' }} onChange={handleChange}>
                            <option >Select Year</option>
                            <option value="2021">2024</option>
                            <option value="2022">2023</option>
                            <option value="2023">2022</option>
                            <option value="2024">2021</option>
                        </CFormSelect>
                        <CFormSelect aria-label="Default select example" style={{ width: '200px', display:'inline-block' }} onChange={handleChange1}>
                            <option >Select Month</option>
                            <option value="1">January</option>
                            <option value="2">February</option>
                            <option value="3" >March</option>
                            <option value="4" >April</option>
                            <option value="5" >May</option>
                            <option value="6" >June</option>
                            <option value="7" >July</option>
                            <option value="8" >August</option>
                            <option value="9" >September</option>
                            <option value="10" >October</option>
                            <option value="11" >November</option>
                            <option value="12" >December</option>
                        </CFormSelect>
                       
                        <CButton className = 'ms-2 mb-1' color="info" type='submit' onClick={handleSubmit}>Go</CButton></div> */}
                        <CModalBody >


                            <div id='body-container'>

                                <CTable className=' mt-1 table table-striped table-hover table-responsive border-secondary table-bordered neumorphic-table' bordered >

                                    <CTableHead className='body_space'>

                                        <tr>

                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>S.no</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Scheme Id</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Scheme type</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Scheme name</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>No.Of HT</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center' ><span className='sch_name'>Demand Amount<br />(Rupees in Lakhs)</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>No.Of LT</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Demand Amount<br />(Rupees in Lakhs)</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Demand Amount<br />(Average per month cost)</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Total Conne ctions</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>EB <br />Total Amount<br />(Rupees in Lakhs)</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>DCB Demand Raised<br />(Rupees in Lakhs)</span></CTableHeaderCell>
                                            <CTableHeaderCell scope="col" className='text-center'><span className='sch_name'>Percentage of EB to DCB</span></CTableHeaderCell>
                                        </tr>


                                    </CTableHead>


                                    {!error ?
                                        <CTableBody >


                                            {tableRow.map((element, index) => (

                                                <CTableRow key={"row_" + index} >
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{index + 1}</span></CTableDataCell>
                                                    <CTableDataCell className='text-center'><span className='sch_sub_name'>{element.scheme_id}</span></CTableDataCell>
                                                    <CTableDataCell className='text-start'><span className='sch_sub_name'>{element.scheme_type_desc}</span></CTableDataCell>
                                                    <CTableDataCell className='text-start'><span className='sch_sub_name'>{element.scheme_name}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end' onClick={() => twadDetails(element.scheme_id, "schemewise", "HT", element.scheme_name, '')}><span className='sch_sub_name'>{element.ht_consumer_count}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{element.ht_amount}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end' onClick={() => twadDetails(element.scheme_id, "schemewise", "LT", element.scheme_name, '')}><span className='sch_sub_name'>{element.lt_consumer_count}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{element.lt_amount}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{element.average_lt_amount}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{element.ht_consumer_count + element.lt_consumer_count}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end'><span className='sch_sub_name'>{element.total_amount}</span></CTableDataCell>
                                                    <CTableDataCell className='text-end' onClick={() => twadDetails(element.scheme_id, "dcb_breakup", '', element.scheme_name, 'dcb', element.dcb_demand)}><span className='sch_sub_name'>{element.dcb_demand === null ? 0.0 : element.dcb_demand}</span></CTableDataCell>
                                                    <CTableDataCell className='text-center' ><span className='sch_sub_name'>{element.percentage}</span></CTableDataCell>


                                                </CTableRow>



                                            ))}
                                            <CTableRow>
                                                <CTableDataCell><b>Total</b></CTableDataCell>
                                                <CTableDataCell></CTableDataCell>
                                                <CTableDataCell></CTableDataCell>
                                                <CTableDataCell></CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.ht_consumer_count, 0)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.ht_amount, 0).toFixed(2)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.lt_consumer_count, 0)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.lt_amount, 0).toFixed(2)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.average_lt_amount, 0).toFixed(2)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.ht_consumer_count + element.lt_consumer_count, 0)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.total_amount, 0).toFixed(2)}</CTableDataCell>
                                                <CTableDataCell className='text-end'>{tableRow.reduce((total, element) => total + element.dcb_demand, 0).toFixed(2)}</CTableDataCell>
                                                <CTableDataCell></CTableDataCell>


                                            </CTableRow>

                                        </CTableBody> : <><h4 className='mt-3 text-info'>No data Found</h4></>}

                                </CTable>
                            </div>
                        </CModalBody>

                    </div>

                    <CModal
                        fullscreen
                        visible={visible}
                        onClose={() => setVisible(false)}  >
                        <CModalHeader closeButton>


                        </CModalHeader>
                        <CModalBody style={{ maxHeight: '100%', overflowY: 'auto', paddingTop: 0 }}>
                            {/* <div className='justify-content-end d-flex mt-2'>
                                <CButton onClick={exportSubTableData} color='warning' className='rounded'>
                                    <CIcon icon={cilCloudDownload} className='text-white' />
                                    <span className='text-white mx-2'>Export Data</span>
                                </CButton>
                            </div> */}
                            <h4>Scheme Name - {schemeName}</h4>
                            <div className='pt-3' >
                                <CTable className='table table-striped table-hover table-responsive border-secondary table-bordered neumorphic-table'>
                                    <CTableHead className='body_space'>
                                        <CTableRow className='text-center'>
                                            <CTableHeaderCell scope="col">S.No</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Consumer Number</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Location</CTableHeaderCell>

                                            <CTableHeaderCell scope="col">Scheme</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Location Type</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Component Name</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Beneficiary Type</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">District Name</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Block Name</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Beneficiary Name</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Connection Status</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Amount <br />(in Rupees)</CTableHeaderCell>
                                        </CTableRow>
                                    </CTableHead>
                                    <CTableBody className='body_space'>
                                        {tableTwad.map((element, index) => (
                                            <CTableRow key={"row_" + index}>
                                                <CTableDataCell >{serialNumberTwad++}</CTableDataCell>
                                                <CTableDataCell>{element?.consumer_number}</CTableDataCell>

                                                <CTableDataCell>{element?.location?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.listofschemas?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.location_type?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.component_name?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.beneficiary_type?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.district_name?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.block_name?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.beneficiary_name?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell>{element?.connection_status?.replace('-1', "")}</CTableDataCell>
                                                <CTableDataCell >{element.type_of_connection === 'HT' ? element?.ht_amount : element?.lt_amount}</CTableDataCell>
                                            </CTableRow>
                                        ))}

                                    </CTableBody>


                                </CTable>
                            </div>
                        </CModalBody>
                    </CModal>

                    <CModal
                        fullscreen
                        visible={dcbBreakup}
                        onClose={() => setDcbBreakUp(false)}  >
                        <CModalHeader closeButton>


                        </CModalHeader>
                        <CModalBody style={{ maxHeight: '100%', overflowY: 'auto', paddingTop: 0 }}>
                            <h4>Scheme Name - {schemeName}</h4>
                            <div className='pt-3' >
                                <CTable className='table table-striped table-hover table-responsive border-secondary table-bordered neumorphic-table'>
                                    <CTableHead className='body_space'>
                                        <CTableRow className='text-center'>
                                            <CTableHeaderCell scope="col " colSpan="1">S.No</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Eb Scheme Id</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Dcb Scheme Id</CTableHeaderCell>
                                            <CTableHeaderCell scope="col" colSpan="7">Dcb Scheme Name</CTableHeaderCell>
                                            <CTableHeaderCell scope="col" colSpan="1">Number of Beneficiaries</CTableHeaderCell>
                                            <CTableHeaderCell scope="col">Demand<br />(Rupees in Lakhs) </CTableHeaderCell>
                                        </CTableRow>
                                    </CTableHead>
                                    <CTableBody className='body_space'>
                                        {tableTwad.map((element, index) => (
                                            <CTableRow key={"row_" + index}>
                                                <CTableDataCell colSpan="1" className='text-center'>{element.sl_no}</CTableDataCell>
                                                <CTableDataCell className='text-center' >{element.eb_scheme_id}</CTableDataCell>
                                                <CTableDataCell className='text-center' >{element.scheme_sno}</CTableDataCell>
                                                <CTableDataCell colSpan="7" >{element.dcb_scheme_name}</CTableDataCell>
                                                <CTableDataCell colSpan="1" className='text-center' >{element.beneficiary_count}</CTableDataCell>
                                                <CTableDataCell className='text-center' >{element.demand}</CTableDataCell>
                                            </CTableRow>
                                        ))}

                                        <CTableRow >
                                            <CTableDataCell  ></CTableDataCell>
                                            <CTableDataCell colSpan="1" className='text-center' ><b>Total</b></CTableDataCell>
                                            <CTableDataCell></CTableDataCell>
                                            <CTableDataCell colSpan="7"></CTableDataCell>
                                            <CTableDataCell className='text-center'>{tableTwad.reduce((total, element) => total + element.beneficiary_count, 0)}</CTableDataCell>
                                            <CTableDataCell className='text-center'>{tableTwad.reduce((total, element) => total + element.demand, 0)}</CTableDataCell>                                            </CTableRow>
                                    </CTableBody>


                                </CTable>
                            </div>
                        </CModalBody>
                    </CModal>






                </div>)

            }
        </div>


    )
}

export default ReportSchemeWise